/**  */
export interface WorldScene {
  id: string;
  name: string;
  icon: string;
  keywords: string[];
  description: string;
  triggerKeyword: string; // 
}

/**  */
export interface WorldCharacter {
  id: string;
  name: string;
  icon: string;
  gender: '' | '';
  identity: string;
  type: 'main' | 'npc';
  traits?: string[];
  relationship?: string;
}

/**  */
export interface WorldSetting {
  id: string;
  title: string;
  icon: string;
  category: 'background' | 'technology' | 'supernatural' | 'item';
  content: string[];
}

